  export default {
    data() {
      return {
        list: [],
				tags:[{name:'餐厅A'},{name:'餐厅B'}],
        tabCur: 0,
				listData:[]
      };
    },
    mounted() {
      uni.showLoading({
        title: '加载中...',
        mask: true
      });
      let list = [{}];
      for (let i = 1; i < 26; i++) {
        list[i] = {};
        list[i].name =  '套餐' + i;
        list[i].id = i;
      }
      this.listData[0] = list;
      this.listData[1] = [{name:"套餐12",name:'套餐13'}];
      this.list = this.listData[0];
      uni.hideLoading()
    },
    methods: {
      TabSelect(e) {
        this.tabCur = e.currentTarget.dataset.id;
        this.list = this.listData[this.tabCur]
      },
      VerticalMain(e) {
      }
    },
  }
